<template>
  <div class="block1">
    <el-form>
      <el-form-item>
        <h1></h1>
      </el-form-item>

      <el-date-picker
        class="pot"
        v-model="datenumber"
        type="daterange"
        range-separator="~"
        start-placeholder="StartDate"
        end-placeholder="EndDate"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
        @change="chooseTimeRange"
      ></el-date-picker>

      <el-button class="fis" icon="el-icon-search" type="primary" @click="getData()">Search</el-button>
      <el-date-picker
        v-model="adatenum"
        class="pot"
        type="date"
        placeholder="Choose a date"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
        @change="aDateChange"
      ></el-date-picker>
      <el-button class="fis" icon="el-icon-search" type="success" @click="getMapData()">Search</el-button>

      <!-- <el-form-item> -->

      <ve-line
        :toolbox="toolbox"
        :data-empty="dataEmpty"
        :loading="loading"
        :data="chartData"
        :settings="chartSettings"
        :extend="extend"
        :legend="legend"
        :tooltip="tooltip"
        width="840px"
        height="530px"
      ></ve-line>

      <ve-map
        :data="mapData"
        :data-empty="mapdataEmpty"
        :loading="maploading"
        :events="mapEvents"
        :settings="mapSettings"
        :legend="legend"
        :extend="extend"
        class="adjustmap"
        width="600px"
        height="530px"
      ></ve-map>

      <!-- </el-form-item> -->
    </el-form>
  </div>
</template>

<script>
import "v-charts/lib/style.css";
export default {
  data() {
    cc: 0;
    this.mapEvents = {
      click: v => {}
    };
    this.mapSettings = {
      selectedMode: "single",
      labelMap: {
        pos: "位置",
        min: "最低气温",
        max: "最高气温",
        avg: "平均气温"
      },
      position: "china",
      label: false,
      itemStyle: {
        normal: {
          borderColor: "#00f"
        }
      }
      // zoom: 1.2
    };
    this.toolbox = {
      feature: {
        magicType: { type: ["line", "bar"] },
        saveAsImage: {}
      }
    };
    this.chartSettings = {
      // scale: [false, true],
      // xAxisType: 'time',
      labelMap: {
        date: "日期",
        min: "最低气温",
        max: "最高气温",
        avg: "平均气温"
      },
      // metrics: ["最高气温", "最低气温", "平均气温"],
      // stack: { 用户: ["最高气温", "最低气温", "平均气温"] },
      area: true
    };

    this.extend = {
      backgroundColor: "#FFFFFF",
      //x轴文本设置
      // "xAxis.0.axisLabel.rotate": 30, //x轴文本倾斜
      // 'xAxis.0.axisLine.lineStyle.color': "red",//x轴文本改变颜色
      "xAxis.0.axisLabel.color": "#E6A23C", //x轴文本颜色
      "xAxis.0.axisLabel.fontSize": 16, //x轴文本字体大小
      "xAxis.0.axisLabel.fontFamily": "Arial", //x轴文本字体系列
      "xAxis.0.axisLabel.fontWeight": "bold", //x轴文本字体粗细
      "xAxis.0.axisLabel.align": "center", //x轴文本字体位置

      //y轴文本设置
      // 'yAxis.0.axisLine.lineStyle.color': "green",//y轴文本改变颜色
      "yAxis.0.axisLabel.color": "#F56C6C", //y轴文本改变颜色
      "yAxis.0.axisLabel.fontSize": 16 //x轴文本字体大小
      // "yAxis.0.axisName": "单位°C" //x轴文本字体大小
    };

    this.tooltip = {
      trigger: "axis",
      backgroundColor: "#0D1B42", //鼠标移动到图上面时，显示的背景颜色
      padding: 15, //定义内边距
      formatter: function(params) {
        //自定义函数修改折线图给数据加单位
        // console.log(params);
        var str = ""; //声明一个变量用来存储数据
        str += "<div>" + params[0].name + "</div>"; //显示日期的函数
        for (var i = 0; i < params.length; i++) {
          // this.cc = params[i].data[1]
          // console.log(params[i].data)
          //图显示的数据较多时需要循环数据，这样才可以成功的给多条数据添加单位
          str +=
            '<div style="color:' +
            params[i].color +
            '"><span>' +
            params[i].seriesName +
            "</span> : <span>" +
            (params[i].data[1] ? params[i].data[1] + "°C" : "暂无") +
            "</span></div>";
        }
        return str;
      }
    };
    this.legend = {
      textStyle: {
        //图例文字的样式
        color: "#000000",
        fontSize: 17,
        fontWeight: "bold"
      }
    };

    // this.grid = {
    //   y: 60
    // };

    // printStr: "";

    return {
      s: "", //日期字符串
      s1: "",
      s2: "",
      adate: "", //选择的一个日期
      dataEmpty: true,
      mapdataEmpty: true,
      datenumber: null,
      adatenum: null,
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      mapData: {
        columns: ["pos", "min", "avg", "max"],
        rows: [
          // { pos: "吉林", " 人口": 123 },
          // { pos: "北京", " 人口": 1223 },
          // { pos: "上海", " 人口": 2123 },
          // { pos: "浙江", " 人口": 4123 }
          // { pos: "广西", min: 12, avg: 10, max: 11 },
          // { pos: "北京", min: 35, avg: 32, max: 34 },
          // { pos: "上海", min: 29, avg: 26, max: 24 },
          // { pos: "广东", min: 17, avg: 14, max: 16 },
          // { pos: "浙江", min: 45, avg: 42, max: 43 },
          // { pos: "吉林", min: 38, avg: 31, max: 36 }
        ]
      },
      chartData: {
        columns: ["date", "min", "avg", "max"],

        rows: [
          // { date: "2020/7/8", min: 12, avg: 10, max: 11 },
          // { date: "Day 2", min: 35, avg: 32, max: 34 },
          // { date: "Day 3", min: 29, avg: 26, max: 24 },
          // { date: "Day 4", min: 17, avg: 14, max: 16 },
          // { date: "Day 6", min: 45, avg: 42, max: 43 },
          // { date: "Day 7", min: 38, avg: 31, max: 36 }
        ]
      },
      loading: false,
      maploading: false
      // dataEmpty: false
    };
  },
  mounted() {
    // this.getData();
    this.dataEmpty = true;
    this.mapdataEmpty = true;
  },
  methods: {
    getMapData() {
      this.mapdataEmpty = false;
      this.maploading = true;
      console.log("getdata....");

      this.$axios
        .post("/pickdate", {
          date: this.adate
        })
        .then(res => {
          console.log(res.data);
          this.loading = false;
          this.mapData.rows = res.data;
        })
        .catch(failResponse => {});
    },
    getData() {
      this.dataEmpty = false;
      this.loading = true;
      console.log("getdata....");

      this.$axios
        .post("/pickdate", {
          date1: this.s1,
          date2: this.s2
        })
        .then(res => {
          console.log(res.data);
          this.loading = false;
          this.chartData.rows = res.data;
        })
        .catch(failResponse => {});

      // this.chartData.rows = [
      // { 'date': "2020/7/8", 'min': 12, 'avg': 10, 'max': 11 },
      // { date: "Day 2", min: 35, avg: 32, max: 34 },
      // { date: "Day 3", min: 29, avg: 26, max: 24 },
      // { date: "Day 4", min: 17, avg: 14, max: 16 },
      // { date: "Day 6", min: 45, avg: 42, max: 43 },
      // { date: "Day 7", min: 38, avg: 31, max: 36 }
      // ];
      // this.loading = false;
    },
    created() {
      this.getData();
    },
    chooseTimeRange(t) {
      this.s = t.join();
      const split = this.s.split(",");

      // console.log(split[0]); //打印第一个日期
      this.s1 = split[0];
      this.s2 = split[1];

      //   this.printStr = t;
    },
    aDateChange(t) {
      // console.log(t);
      this.adate = t;
      console.log(this.adate);
    }
  }
};
</script>>
 <style>
.pot {
  top: 10px;
  left: 500px;
}
.fis {
  padding: 12px 10px;
  position: relative;
  top: -30px;
  left: 865px;
}
.adjustmap {
  top: -530px;
  left: 840px;
}
.block1 {
  line-height: 40px;
  width: 300px;
  height: 80px;
  color: white;
  /* position: absolute; */
  /* float: center; */
  /* background-color: #0c2152; */
}
</style>




