import Vue from 'vue'
import Router from 'vue-router'
import Welcome from '@/components/Welcome'
import login from '@/components/Login'
import register from '@/components/Register'
import pickdate from '@/components/PickDate'
import admin from '@/components/Admin'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
    {
      path: '/pickdate',
      name: 'pickdate',
      component: pickdate
    },
    {
      path: '/admin',
      name: 'admin',
      component: admin
    }

  ]
})



