<template>
  <div>
    <h1 style="text-align: center; color: #E6A23C">用户管理中心</h1>
    <el-row class="table-grid-content">
      <el-col :span="18" class="grid">
        <el-input v-model="input" placeholder="请输入搜索内容"></el-input>
      </el-col>
      <el-col :span="3" class="grid" :gutter="9">
        <el-button type="success" icon="el-icon-search">搜索</el-button>
      </el-col>
      <el-col :span="2">
        <el-button type="primary" icon="el-icon-circle-plus-outline
" @click="addMembers()">增加</el-button>
      </el-col>
    </el-row>

    <el-table
      :data="tables"
      :stripe="true"
      :border="true"
      width="100%"
      height="550"
      v-loading="loading"
      :data-empty="dataEmpty"
    >
      <el-table-column fixed label="序号" type="index" width="60" align="center"></el-table-column>
      <!-- <el-table-column label="日期" prop="date"></el-table-column> -->
      <el-table-column label="用户名" prop="username"></el-table-column>
      <el-table-column label="密码" prop="password"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            @click="modifyData(scope.$index, scope.row)"
          >修改</el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            @click="deleteData(scope.$index,tableData)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :visible.sync="centerDialogVisible"
      :before-close="handleClose"
      :modal-append-to-body="false"
    >
      <el-form :model="editForm" :rules="rules">
        <!-- <el-form-item label="日期" :picker-options="pickerOptions">
          <el-date-picker
            v-model="editForm.date"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>-->
        <el-form-item label="用户名" prop="username">
          <el-input v-model="editForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="editForm.password"></el-input>
        </el-form-item>
      </el-form>
      <div>
        <el-button @click="closeDialog()">取消</el-button>
        <el-button type="primary" @click="sumbitEditRow()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog
      :visible.sync="isAddMembers"
      :modal-append-to-body="false"
      :before-close="handleClose"
    >
      <el-form :model="addForm" :rules="rules">
        <!-- <el-form-item label="日期" :picker-options="pickerOptions">
          <el-date-picker
            v-model="addForm.date"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>-->
        <el-form-item label="用户名" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
      </el-form>
      <div>
        <el-button @click="closeDialog()">取消</el-button>
        <el-button type="primary" @click="sumbitAddRow()">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import { musicBroadcastingDetails } from '@/api/index.js'
import axios from "axios";
var _index;
export default {
  name: "admin",
  data() {
    return {
      centerDialogVisible: false,
      isAddMembers: false,
      editForm: {
        username: "",
        password: ""
      },
      addForm: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          {
            required: true,
            message: "请输入用户名",
            trigger: ["blur", "change"]
          }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: ["blur", "change"] }
        ]
      },
      searchData: "",
      input: "",
      id: "",
      loading: false,
      dataEmpty: true,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      tableData: [
        // {
        //   // date: "2020-05-02",
        //   username: "隔壁老王",
        //   password: "123"
        // },
        // {
        //   // date: "2020-05-04",
        //   username: "隔壁老张",
        //   password: "12314"
        // },
        // {
        //   // date: "2020-05-01",
        //   username: "隔壁老李",
        //   password: "123312"
        // }
      ]
    };
  },
  mounted() {
    this.dataEmpty = false;
    this.loading = true;
    this.$axios
      .post("/admin", {
        str: "all" //删除时传用户名
        // password: this.tableData[index].password
      })
      .then(res => {
        this.tableData = res.data;
        this.loading = false;
        this.$message({
          message: "用户加载成功！",
          type: "success"
        });
      })
      .catch(failResponse => {});
    // // console.log("我进来了！")
  },

  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
          this.$message({
            message: "没有进行任何操作！",
            type: "info"
          });
        })
        .catch(_ => {});
    },
    deleteData(index, row) {
      this.$axios
        .post("/admin", {
          mys1: "delete",
          username: this.tableData[index].username //删除时传用户名
          // password: this.tableData[index].password
        })
        .then(res => {
          if (res.data === 1) {
            this.tableData.splice(index, 1);
            this.$message({
              message: "删除成功！",
              type: "success"
            });
          }
        })
        .catch(failResponse => {});
      // this.addForm = Object.assign({}, row);
      console.log("row的值是：", this.tableData[index].username); //删除行的数据

      console.log("进行了删除操作");
      console.log("index的值是：" + index);
      // console.log("row的值是：", this.addForm.username);
    },
    modifyData(index, row) {
      this.centerDialogVisible = true;
      this.editForm = Object.assign({}, row); //重置对象
      _index = index;
      console.log("index的值：" + index);
      console.log("_index的值：" + _index);
      console.log("row的值是：", this.editForm); //代表选择的这一行的数据
    },
    sumbitEditRow() {
      var editData = _index;
      // console.log("editData的值" + this.editForm);
      var smy = this.tableData[editData].username;

      console.log("改之前： " + smy);

      var usernameaftermy = this.editForm.username;
      var passwordaftermy = this.editForm.password;
      // this.tableData[editData].username = this.editForm.usernam;
      // // this.tableData[editData].date = this.editForm.date;
      // this.tableData[editData].password = this.editForm.password;
      // this.centerDialogVisible = false;
      // console.log("改之后： " + this.tableData[editData].username);
      this.$axios
        .post("/admin", {
          mys2: "modify",
          usernamebefore: smy, //改之前
          usernameafter: usernameaftermy,
          password: passwordaftermy
          // password: this.tableData[index].password
        })
        .then(res => {
          if (res.data === 2) {
            this.$message({
              message: "用户名已存在！",
              type: "error"
            });
            this.editForm.username = "";
          } else if (res.data === 1) {
            this.tableData[editData].username = this.editForm.usernam;
            // this.tableData[editData].date = this.editForm.date;
            this.tableData[editData].password = this.editForm.password;
            this.centerDialogVisible = false; //图表消失
            this.$message({
              message: "编辑成功！",
              type: "success"
            });
          }
        })
        .catch(failResponse => {});
      // console.log("数据：" + this.editForm.date);
      // console.log("对象数组", this.tableData);
    },
    closeDialog() {
      this.centerDialogVisible = false;
      this.isAddMembers = false;
      console.log("editfrom", this.editForm);
      console.log("addfrom", this.addForm);
      this.$message({
        message: "没有进行任何操作！",
        type: "info"
      });
    },
    addMembers() {
      // this.loading = true;
      this.isAddMembers = true; //显示对话框
      this.addForm = {
        username: "",
        // date: "",
        password: ""
      };
    },
    sumbitAddRow() {
      //增加
      this.tableData = this.tableData || [];
      console.log("表格是:" + this.tableData);
      // this.tableData.push({
      //   //增加时传用户名加密码
      //   username: this.addForm.username,
      //   // date: this.addForm.date,
      //   password: this.addForm.password
      // });
      // this.isAddMembers = false;
      // console.log(this.addForm.username + ',' + this.addForm.password)
      this.$axios
        .post("/admin", {
          mys3: "add",
          username: this.addForm.username,
          password: this.addForm.password
        })
        .then(res => {
          if (res.data === 2) {
            this.$message({
              message: "用户名已存在！",
              type: "error"
            });
            this.addForm.username = "";
          } else if (res.data === 1) {
            this.tableData.push({
              //增加时传用户名加密码
              username: this.addForm.username,
              // date: this.addForm.date,
              password: this.addForm.password
            });
            this.isAddMembers = false;
            this.$message({
              message: "新用户添加成功！",
              type: "success"
            });
          }
        })
        .catch(failResponse => {});
      // console.log("新增的日期：" + this.addForm.date);
    }
  },
  computed: {
    tables() {
      const input = this.input;
      if (input) {
        console.log("input输入的搜索内容：" + this.input);
        return this.tableData.filter(data => {
          console.log("object:" + Object.keys(data));
          return Object.keys(data).some(key => {
            return (
              String(data[key])
                .toLowerCase()
                .indexOf(input) > -1
            );
          });
        });
      }
      return this.tableData;
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.table-grid-content {
  border-radius: 4px;
  height: 50 px;
  background: #ebeef5;
  padding: 10px;
}
</style>



